<template>
  <div>
    <div class="row">
      <div class="col-4">
        <h3 class="fw-bold">All Payments</h3>
      </div>
      <div class="col-2">
        <div class="my-2">
          <v-btn color="error" dark large>
            Export Excel
          </v-btn>
        </div>
      </div>
      <div class="col-6">
        <b-form-input size="lg" class="" placeholder="Search">
        </b-form-input>
      </div>
      <v-row>
        <v-col class="d-flex" cols="4" sm="4">
          <v-select :items="items" label="City"></v-select>
        </v-col>
        <v-col class="d-flex" cols="4" sm="4">
          <v-select :items="items" label="State"></v-select>
        </v-col>
        <v-col class="d-flex" cols="4" sm="4">
          <v-select :items="items" label="Payment Type"></v-select>
        </v-col>
      </v-row>
      <v-row>
        <v-col cols="4" sm="6" md="4">
          <v-menu v-model="menu1" :close-on-content-click="false" :nudge-right="40"
                  transition="scale-transition" offset-y min-width="auto">
            <template v-slot:activator="{ on, attrs }">
              <v-text-field v-model="date1" label="Date From" prepend-icon="mdi-calendar"
                            v-bind="attrs" v-on="on"></v-text-field>
            </template>
            <v-date-picker v-model="date1" @input="menu1 = false"></v-date-picker>
          </v-menu>
        </v-col>
        <v-col cols="4" sm="6" md="4">
          <v-menu v-model="menu2" :close-on-content-click="false" :nudge-right="40"
                  transition="scale-transition" offset-y min-width="auto">
            <template v-slot:activator="{ on, attrs }">
              <v-text-field v-model="date2" label="Date To" prepend-icon="mdi-calendar"
                            v-bind="attrs" v-on="on"></v-text-field>
            </template>
            <v-date-picker v-model="date2" @input="menu2 = false"></v-date-picker>
          </v-menu>
        </v-col>
        <v-col class="d-flex" cols="4" sm="4">
          <v-select :items="items" label="Plan"></v-select>
        </v-col>
      </v-row>
      <div class="">
        <v-data-table :headers="headers" :items="desserts" sort-by="calories"
                      :single-select="singleSelect" show-select class="elevation-1">
          <template v-slot:top>
            <v-divider class="mx-4" inset vertical></v-divider>
            <v-spacer></v-spacer>
            <v-dialog v-model="dialog" max-width="500px">
              <!-- <template v-slot:activator="{ on, attrs }">
                  <v-btn color="primary" dark class="mb-2" v-bind="attrs" v-on="on">
                      New Item
                  </v-btn>
              </template> -->
              <v-card>
                <v-card-title>
                  <span class="text-h5">{{ formTitle }}</span>
                </v-card-title>

                <v-card-text>
                  <v-container>
                    <v-row>
                      <v-col cols="12" sm="6" md="4">
                        <v-text-field v-model="editedItem.name"
                                      label="User Name"></v-text-field>
                      </v-col>
                      <v-col cols="12" sm="6" md="4">
                        <v-text-field v-model="editedItem.rating"
                                      label="E-mail"></v-text-field>
                      </v-col>
                      <v-col cols="12" sm="6" md="4">
                        <v-text-field v-model="editedItem.phone"
                                      label="Phone Number"></v-text-field>
                      </v-col>
                      <v-col cols="12" sm="6" md="4">
                        <v-text-field v-model="editedItem.city"
                                      label="City"></v-text-field>
                      </v-col>
                      <v-col cols="12" sm="6" md="4">
                        <v-text-field v-model="editedItem.rating"
                                      label="Username"></v-text-field>
                      </v-col>
                      <v-col cols="12" sm="6" md="4">
                        <v-text-field v-model="editedItem.reviewcount"
                                      label="Review Count"></v-text-field>
                      </v-col>
                    </v-row>
                  </v-container>
                </v-card-text>

                <v-card-actions>
                  <v-spacer></v-spacer>
                  <v-btn color="blue darken-1" text @click="close">
                    Cancel
                  </v-btn>
                  <v-btn color="blue darken-1" text @click="save">
                    Save
                  </v-btn>
                </v-card-actions>
              </v-card>
            </v-dialog>
            <v-dialog v-model="dialogDelete" max-width="500px">
              <v-card>
                <v-card-title class="text-h5">Are you sure you want to delete this
                  item?
                </v-card-title>
                <v-card-actions>
                  <v-spacer></v-spacer>
                  <v-btn color="blue darken-1" text @click="closeDelete">Cancel</v-btn>
                  <v-btn color="blue darken-1" text @click="deleteItemConfirm">OK</v-btn>
                  <v-spacer></v-spacer>
                </v-card-actions>
              </v-card>
            </v-dialog>
          </template>
          <template v-slot:[`item.action`]="{ item }">
            <v-icon small class="mr-2" @click="editItem(item)">
              mdi-pencil
            </v-icon>
            <v-icon small @click="deleteItem(item)"> mdi-delete</v-icon>
          </template>
          <template v-slot:[`item.active`]="{ item }">
            <v-icon small class="mr-2" @click="editItem(item)">
              mdi-check
            </v-icon>
          </template>
          <template v-slot:no-data>
            <v-btn color="primary" @click="initialize"> Reset</v-btn>
          </template>
        </v-data-table>
        <div class="text-muted">
          <v-btn dark large>DELETE SELECTED</v-btn>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default{
    name: "PaymentsView",
    components: {  },
    data: () => ({
        items: ['Foo', 'Bar', 'Fizz', 'Buzz'],
        menu1: false,
        menu2: false,
        modal: false,
        dialog: false,
        dialogDelete: false,
        headers: [
            { text: "Email", value: "email" },
            { text: "Payment Mode", value: "payment" },
            { text: "Amount", value: "amount" },
            { text: "City", value: "city" },
            { text: "Plan", value: "plan" },
            { text: "State", value: "state" },
            { text: "Reseller Name", value: "reseller_name" },
            { text: "Payment Date", value: "created_at" },
            { text: "Actions", value: "action", sortable: false },
        ],
        desserts: [],
        editedIndex: -1,
        editedItem: {
            name: "",
            rating: "",
            phone: "",
            city: "",
            review: "",
            reviewcount: "",
            created_at: "",
        },
        defaultItem: {
            name: "",
            email: "",
            phone: "",
            city: "",
            review: "",
            reviewcount: "",
            created_at: "",
        },
    }),

    computed: {
        formTitle() {
            return this.editedIndex === -1 ? "New Item" : "Edit Item";
        },
    },

    watch: {
        dialog(val) {
            val || this.close();
        },
        dialogDelete(val) {
            val || this.closeDelete();
        },
    },

    created() {
        this.initialize();
    },

    methods: {
        initialize() {
            this.desserts = [
                {
                    email: "Frozen@gmail.com",
                    payment: "UPI",
                    amount: 1,
                    city: "London",
                    plan: "N/A",
                    state: "London",
                    reseller_name: 'N/A',
                    created_at: "Feb 20, 2023 9:59 AM",
                },
                {
                    email: "Ice cream sandwich",
                    payment: "Cash",
                    amount: 8,
                    city: "London",
                    plan: "N/A",
                    state: "London",
                    reseller_name: 'N/A',
                    created_at: "Feb 23, 2023 10:59 AM",
                },
            ];
        },

        editItem(item) {
            this.editedIndex = this.desserts.indexOf(item);
            this.editedItem = Object.assign({}, item);
            this.dialog = true;
        },

        deleteItem(item) {
            this.editedIndex = this.desserts.indexOf(item);
            this.editedItem = Object.assign({}, item);
            this.dialogDelete = true;
        },

        deleteItemConfirm() {
            this.desserts.splice(this.editedIndex, 1);
            this.closeDelete();
        },

        close() {
            this.dialog = false;
            this.$nextTick(() => {
                this.editedItem = Object.assign({}, this.defaultItem);
                this.editedIndex = -1;
            });
        },

        closeDelete() {
            this.dialogDelete = false;
            this.$nextTick(() => {
                this.editedItem = Object.assign({}, this.defaultItem);
                this.editedIndex = -1;
            });
        },

        save() {
            if (this.editedIndex > -1) {
                Object.assign(this.desserts[this.editedIndex], this.editedItem);
            } else {
                this.desserts.push(this.editedItem);
            }
            this.close();
        },
    },
}
</script>